input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
	* Remove the default vertical scrollbar in IE.
	*/
textarea {
  overflow: auto;
}

/**
	* Give <main>s the proper display value in IE.
	*/
main {
  display: block;
}

/* Hidden
		=====================================================================*/
/**
	* Add the correct display in IE 10-.
	*/
[hidden] {
  display: none !important;
}

/* Next specific stuff
		=====================================================================*/
/**
	* Standardise outline styles
	*/
:focus {
  outline: thin dotted;
  outline-offset: 1px;
}

